import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// import { CrossSellingBean } from './bean/cross-selling.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message, SelectItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DomSanitizer } from '@angular/platform-browser';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, AbstractControl, FormGroup, FormBuilder } from '@angular/forms';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
@Component({
    selector: 'cross-sell-task',
    styleUrls: ['cross-sell-task.component.scss'],
    templateUrl: 'cross-sell-task.component.html',
    providers: [ConfirmationService, Commfunc, CusOperationHttpService]
})

export class CrossSellTaskComponent implements OnInit {
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    dataMap: any = {};
    code: any;
    campaignName: string;
    dataSource: string;
    custNo: string;
    thruDate: string;
    fromDate: string;
    custName: string;
    productName: string;
    recReasons: string;
    mobileNum: string;
    productNo: string;
    userName: string;
    userId: string;
    marketingCampaignId: string;
    dataSourceOptions: any = [];//数据来源
    contactWays: any = [];
    feedbacks: any = [];
    flag: string;
    isHistory: boolean = false;
    isClickYL: boolean = false;//可点
    contactWay:  string;
    feedback:  string;
    executeDate:  string;
    executeCase:  string;
    taskId:  string;
    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public router: Router,
        public activatedRoute: ActivatedRoute,
    ) {
        if (this.activatedRoute.snapshot.params['dataMap']) {
            this.dataMap = JSON.parse(this.activatedRoute.snapshot.params['dataMap']);
        }
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.taskId = JSON.parse(this.activatedRoute.snapshot.params['taskId']);
        // 历史 和 待办 判断-隐藏按钮
        // this.flag = this.activatedRoute.snapshot.params['markFlag'];
        // if (this.flag == '1') {
        //     this.isHistory = false;
        // } else {
        //     this.isHistory = true;
        // }

    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
        this.contactWays = this.code['contactWays'];//联系方式
        this.feedbacks = this.code['dataSourceOptions'];//客户反馈
        this.contactWay = '01';
        this.feedback ='01';
        this.queryCustBaseInfo();
    }
    queryCustBaseInfo() {
        console.log(this.dataMap);
        let param = {
            marketingCampaignId: this.dataMap.requestSeqNo,
            custNo: this.dataMap.custNo,
            productNo: this.dataMap.productNo,
        }
        this.httpService.queryCustBaseInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custNo = data.custBaseInfo.custNo;
                this.custName = data.custBaseInfo.custName;
                this.productName= data.custBaseInfo.productName;
                this.thruDate = data.custBaseInfo.thruDate;
                this.fromDate = data.custBaseInfo.fromDate;
                this.recReasons= data.custBaseInfo.recReasons;
                this.campaignName = data.custBaseInfo.campaignName;
                this.dataSource = data.custBaseInfo.dataSource;
                this.mobileNum = data.custBaseInfo.mobileNum;
                this.productNo = data.custBaseInfo.productNo;
                this.marketingCampaignId = data.custBaseInfo.marketingCampaignId;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    // 提交
    sumbit(){
        if (!this.executeDate || this.executeDate == '') {
			this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '执行日期不能为空' });
			return;
        }
        if (!this.executeCase || this.executeCase == '') {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '执行情况不能为空' });
			return;
		}
        let param = {
            marketingCampaignId: this.marketingCampaignId,
            custNo: this.custNo,
            productNo: this.productNo,
            contactWay: this.contactWay,
            feedback: this.feedback,
            executeDate:  this.commfunc.transDateN(this.executeDate),
            executeCase: this.executeCase,
        }
        this.httpService.updateCustLiaison(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bpmNext();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
     bpmNext() {
        let params = {
            userId: this.userId,
            taskId: this.taskId,
            bussinessMap: {
                dataMap: {

                }
            }
        }
        this.httpService.bpmNextTask(params).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.isClickYL = true;
                this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/workdesk-page'])
            } else {
                this.isClickYL = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.isClickYL = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
     }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    doUp() {
        setTimeout(() => {
            window.history.go(-1);
        }, 500)
    }
}
