//待办及提醒事项查看全部详情
import { Component, OnInit, Inject, forwardRef } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../@uisftech/common/constant/config';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from '../../../../@uisftech/common/service/http.constant';
import { CusOperationHttpService } from '../http/custom-operation/custom-operation.http.service';
import { SiteBean, Detailsite, Addsites, Updatesites } from '../address-administration/bean/address-administration.bean';
import { CustGroupListBean } from '../custom-group/bean/custom-group.bean';
@Component({
	selector: 'app-address',
	templateUrl: './address-administration.component.html',
	styleUrls: ['./address-administration.component.scss'],
	providers: [Commfunc]
})
export class Addressadministration implements OnInit {
	tabMesg: any = tableMessage;//暂无数据
	isaddress: boolean = false;
	isdetail: boolean = false;
	isupdate: boolean = false;
	resultList: any[];
	msgs: any[];
	checkBox: any = [];
	chooseData: any = [];
	zoneIds: any = [];
	checkAll: boolean = false;
	batchDisplay: boolean = false;
	addDisplay: boolean = false;
	totalNum: any;
	filePath: any;
	fileName: any;
	first: any = 1;
	fileList: any = [];//上传文件列表
	UPLOAD: string = TZB_CUS_UPLOAD;//上传模板
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载模板
	orizoneId: any;
	custGroupListBean: CustGroupListBean = new CustGroupListBean();
	Addsites: Addsites = new Addsites();
	Updatesites: Updatesites = new Updatesites();
	Detailsite: Detailsite = new Detailsite();
	SiteBean: SiteBean = new SiteBean();
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRouter: ActivatedRoute,
		public CusOperationHttpService: CusOperationHttpService,

	) {

	}


	ngOnInit() {
		this.SiteBean.pageSize = 10;
		this.SiteBean.pageNum = 1;
		this.query();
	}
	query() {
		let param = {
			zoneId: this.SiteBean.zoneId,
			zoneName: this.SiteBean.zoneName,
			parentZoneId: this.SiteBean.parentZoneId,
			parentZoneName: this.SiteBean.parentZoneName,
			zoneLevel: this.SiteBean.zoneLevel,
			pageSize: this.SiteBean.pageSize,
			pageNum: this.SiteBean.pageNum,
		}
		this.CusOperationHttpService.queryAddressDictionaryList(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.resultList = data.resultList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		})
	}

	paginate(event) {
		//每页显示的条数
		this.SiteBean.pageSize = event.rows * 1;
		//当前页
		this.SiteBean.pageNum = event.page + 1;
		this.query();
	}
	// 单选按钮
	checkBoxClick(i, data) {
		if (data.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
		if (this.chooseData.length == this.resultList.length) {
			this.checkAll = true;
		} else {
			this.checkAll = false;
		}
	}
	// 全选按钮
	checkAllClick() {
		if (this.resultList) {
			for (let i = 0; i < this.resultList.length; i++) {
				if (this.checkAll == true) {
					this.resultList[i].checkBox = true;
					this.chooseData.push(this.resultList[i]);
				}
				else {
					this.chooseData = [];
					this.resultList[i].checkBox = false;
				}
			}
		}
	}
	saveAddress() {
		let param = {
			zoneName: this.Addsites.zoneName,
			parentZoneId: this.Addsites.parentZoneId,
			zoneLevel: this.Addsites.zoneLevel,
			zoneId: this.Addsites.zoneId
		}
		this.CusOperationHttpService.addAddressDictionary(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalNum = data.totalNum;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.isaddress = false;
			} else {
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}), err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		}
	}
	Updatedress() {
		let param = {
			oriZoneId: this.orizoneId,
			zoneName: this.Updatesites.zoneName,
			parentZoneId: this.Updatesites.parentZoneId,
			zoneLevel: this.Updatesites.zoneLevel,
			zoneId: this.Updatesites.zoneId
		}
		this.CusOperationHttpService.updateAddressDictionary(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalNum = data.totalNum;
				this.isupdate = false;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.isupdate = false;
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}), err => {
			this.isupdate = false;
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		}
	}
	closeDetail() {
		this.isdetail = false;
	}
	openUpdate(col) {
		this.orizoneId = col.zoneId;
		this.isupdate = true;
		this.Updatesites = col;
	}
	delete(col) {
		let param = {
			zoneId: col.zoneId
		}
		this.zoneIds = [];
		this.zoneIds.push(param);
		this.CusOperationHttpService.deleteAddressDictionary({ zoneIds: this.zoneIds }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.query();
			} else {
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}), err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		}
	}

	addAddress() {
		this.isaddress = true;
	}
	opendetail(col) {
		this.isdetail = true;
		this.Detailsite = col;
	}
	toLead() {
		this.batchDisplay = true;
	}
	//批量导入
	onBeforeSend(event) {
		// 上传点击,需跨域
		if (event.xhr.response) {
			this.fileList = JSON.parse(event.xhr.response);
			if (this.fileList.length > 1) {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
				return;
			}
			if (this.fileList.hasOwnProperty('errorMsg')) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
				return
			}
			this.filePath = this.fileList[0].fileUrl;
			this.fileName = this.fileList[0].fileName;
		}
		else {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
		}

		//不需要审批
		let param = {
			fileUrl: this.filePath,
			fileName: this.fileName
		}
		this.CusOperationHttpService.importAddressDictionary(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.custGroupListBean.custList = data.failList;
				this.custGroupListBean.failNum = data.failList.length;
				this.custGroupListBean.custList.forEach(e => {
					e['errvel'] = '失败';
				});
				this.msgs = [];
				this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
			}
			else {
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '导入失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		})
	}
	returnCustGroupList() {
		this.batchDisplay = false;
		this.custGroupListBean.custList = null;
		this.custGroupListBean.failNum = null;
	}
	closecust() {
		this.Addsites = new Addsites();
	}
	export() {
		let param = {
			zoneId: this.SiteBean.zoneId,
			zoneName: this.SiteBean.zoneName,
			parentZoneId: this.SiteBean.parentZoneId,
			parentZoneName: this.SiteBean.parentZoneName,
			zoneLevel: this.SiteBean.zoneLevel,
			pageSize: this.SiteBean.pageSize,
			pageNum: this.SiteBean.pageNum,
		}
		this.CusOperationHttpService.exportAddressDictionary(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				let aa = data.fileName;
				window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl);
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	batchDelete() {
		let param = {
			zoneIds: this.chooseData
		}
		this.CusOperationHttpService.deleteAddressDictionary(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				this.chooseData = [];
				this.query();
			} else {
				this.msgs = [];
				data.returnCode.code ? data.returnCode.code : '查询失败！';
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}), err => {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
		}
	}
}


