import { Component, OnInit, Input, OnChanges, Output, EventEmitter, Inject, forwardRef } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
// import { TranType, ClientState, SubBussType, RecycleMode, CountInterrstFlag, RepayType, LoanDetailType, LoanType, LoanForm, LoanNature, CURRENCY, GuaranteeMode, BussType,InvestIndustry } from '../../../../constant/custView.codeValue';
import { Router, ActivatedRoute } from '@angular/router';
import { LoanDetailBean } from 'app/pages/tzb/custom/custom-view/bean/custom-view.bean';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
//贷款明细信息
@Component({
  selector: 'loan-details2',
  templateUrl: './loan-details2.component.html',
  styleUrls: ['./loan-details2.component.scss'],
  providers: [Commfunc]
})
export class LoanDetails2Component implements OnInit {

  bankSystemType: any;
  msgs: Message[] = [];
  myVar: number = 0;
  pageNum: number = 1;
  pageSize: number = 10;
  loanDetail: LoanDetailBean = new LoanDetailBean();//贷款明细信息
  investIndustryOptions: any = [];//行业投向
  repaymentsitems: any = [];//还款信息
  repaymentsitemspageSize: number;
  repaymentsitemspageNum: number;
  repaymentsitemstotalNum: number;
  loanGuarInfoItems: any[];//担保信息
  loanGuarInfopageSize: number;
  loanGuarInfopageNum: number;
  loanGuarInfototalNum: number;
  borrowerItems: any[];//共同借款人
  borrowerpageSize: number;
  borrowerpageNum: number;
  borrowertotalNum: number;
  accumulationItems: any[];//积数冲减
  accumulationpageSize: number;
  accumulationpageNum: number;
  accumulationtotalNum: number;
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  bussTypeOptions: any[] = [];//业务类型
  guaranteeModeOptions: any[] = [];//担保方式
  currencyOptions: any[] = [];//币种
  loanNatureOptions: any[] = [];//贷款性质
  loanFormOptions: any[] = [];//贷款形式
  loanTypeOptions: any[] = [];//贷款类型
  repayTypeOptions: any[] = [];//还款方式
  countInterrstFlagOptions: any[] = [];//计息标志
  recycleModeOptions: any[] = [];//回收方式
  subBussTypeOptions: any[] = [];//贷款分类
  clientStateOptions: any[] = [];//客户状态
  tranTypeOptions: any[] = [];//交易类型
  userId: any;
  user: any;
  user2: any;
  orgId: string = '';
  // userId: string = '';
  userName: string = '';
  orgName: string = '';
  targetId: string = '';
  date = new Date();
  newdate: string;
  tellSeqNo: string;
  clientNo: string;
  biaozhi: string;
  show: boolean = true;
  code: any;
  custNum: boolean = true;
  activeShow: number;//选中的tab页的样式
  inValue: any;
  iousNum: string;//借据号
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public router: Router,
    public routerInfo: ActivatedRoute,
  ) {
    this.codeVal();
    this.investIndustryOptions = this.code['InvestIndustry'];
    this.bussTypeOptions = this.code['BussType'];
    this.guaranteeModeOptions = this.code['GuaranteeMode'];
    this.currencyOptions = this.code['CURRENCY'];
    this.loanNatureOptions = this.code['LoanNature'];
    this.loanFormOptions = this.code['LoanForm'];
    this.loanTypeOptions = this.code['LoanType'];
    this.repayTypeOptions = this.code['RepayType'];
    this.countInterrstFlagOptions = this.code['CountInterrstFlag'];
    this.recycleModeOptions = this.code['RecycleMode'];
    this.subBussTypeOptions = this.code['LoanDetailType'];
    this.clientStateOptions = this.code['ClientState'];
    this.tranTypeOptions = this.code['TranType'];

    //-----------------------------------------------------上面是码值
    // this.userId=JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    if (this.user2.userName) {
      this.userName = this.user2.userName;
    }
    if (this.user2.orgName) {
      this.orgName = this.user2.orgName;
    }
    if (this.user.targetId) {
      this.targetId = this.user.targetId;
    }

    let a = this.targetId.split('');
    let b = a[a.length - 1];
    if (b == '0') {
      this.biaozhi = 'tzb';
    } else if (b == '1') {
      this.biaozhi = 'czb';
    }
    this.newdate = this.commfunc.transDate(this.date);

  }
  ngOnInit() {
    this.iousNum = this.routerInfo.snapshot.queryParams.iousNum;
    if (this.routerInfo.snapshot.queryParams.custNo) {//
      let params = {
        custNo: this.routerInfo.snapshot.queryParams.custNo
      };
      this.httpService.selectHoldLoanInfo(params).subscribe(datas => {
        if (datas.returnCode.code == 'success') {
          if (datas.prodInfoList && datas.prodInfoList.length != 0) {
            let data;
            datas.prodInfoList.forEach(element => {
              if(element.iousNum == this.iousNum){
                data = element;
              }
            });
            console.log(data);
            this.inValue = { duebillNo: data.iousNum, custNo: data.custNo, contractNum: data.contractNum, subBusinessType: data.subBusinessType, seqNo: data.seqNo };;//通过路由获得参数 
            this.preset();
          }
        }
        else {
          this.msgs = [];
          datas.returnCode.code ? datas.returnCode.code : '调用服务失败！';
          this.msgs.push({ severity: 'error', summary: '提示', detail: datas.returnCode.message });
        }
      }, err => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
      })
    }
  }

  //初始化
  preset() {
    if (this.commfunc.getSessionDataCH('custNoNum')) {
      this.custNum = false;
    }

    if (this.inValue) {
      if (this.inValue.show && this.inValue.show == 'glgx') {
        this.show = false;
      } else {
        this.show = true;
      }
      if (this.commfunc.getSessionData('commonHeader')) {
        let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
        if (orgId.charAt(0) == 'T') {
          this.bankSystemType = 'TZB';
        } else if (orgId.charAt(0) == 'C') {
          this.bankSystemType = 'CZB';
        } else {
          this.bankSystemType = 'TZB';
        }
      }
      if (this.inValue.custNo) {
        this.inValue['clientNo'] = this.inValue.custNo;
      }
      if (this.inValue.contractNo) {
        this.inValue.contractNum = this.inValue.contractNo;
      }
      // if(this.inValue.iousNum){
      //   this.inValue['duebillNo']= this.inValue.iousNum; 
      //   console.log(this.inValue);
      // }
      let param = {
        duebillNo: this.inValue.duebillNo
      };
      //贷款明细信息
      this.httpService.BZ_0200300000103_LOAN(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.loanDetail = data;
          this.tellSeqNo = this.loanDetail.tellSeqNo;
          this.clientNo = this.loanDetail.clientNo;
          this.httpService.queryTaskByIndtstryAll({ taskTypeId: this.loanDetail.investIndustry, taskTypeKey: 'indtstryType' }).subscribe(data => {
            if (data.returnCode.code == 'success') {
              if (data.taskList.length > 0) {
                this.loanDetail.investIndustry = data.taskList[0].taskTypeName;
              }
            }
          });
          this.httpService.queryTellerById({ tellerId: this.loanDetail.liablePersonName }).subscribe(data => {
            this.loanDetail.liablePersonName = data.tellerName;
          });
          this.httpService.queryTellerById({ tellerId: this.loanDetail.investigatorName }).subscribe(data => {
            this.loanDetail.investigatorName = data.tellerName;
          });
          this.httpService.queryOrgById({ orgId: this.loanDetail.appBranchNo }).subscribe(data => {
            if (data.orgName != null) {
              this.loanDetail.appBranchNo = data.orgName;
            }
          });
          this.httpService.queryOrgById({ orgId: this.loanDetail.manageBranchId }).subscribe(data => {
            if (data.orgName != null) {
              this.loanDetail.manageBranchId = data.orgName;
            }
          });
        } else {
          data.returnCode.message ? data.returnCode.message : '调用服务失败';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
        this.loanDetail.bussType = this.inValue.bussType;
      });
    }
  }



  //还款信息
  refund() {
    let param1 = {
      duebillNo: this.inValue.duebillNo,
      clientNo: this.inValue.clientNo,
      bussType: '0',
      pageSize: this.pageSize,
      pageNum: this.pageNum
    }
    //还款信息
    this.httpService.BZ_0200300000104_LOAN(param1).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.repaymentsitems = data.infoList;
        this.repaymentsitemspageNum = data.pageNum;
        this.repaymentsitemspageSize = data.pageSize;
        this.repaymentsitemstotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }
  //账户积数冲减
  account() {
    let param4 = {
      bankSystemType: this.bankSystemType,
      // clientNo:this.inValue.clientNo,
      duebillNo: this.inValue.duebillNo,
      // clientNo:'1300107966',
      pageSize: this.pageSize,
      pageNum: this.pageNum
    }
    //账户积数冲减
    this.httpService.BZ_1100300003001_LOAN(param4).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.accumulationItems = data.infoList;
        this.accumulationpageNum = data.pageNum;
        this.accumulationpageSize = data.pageSize;
        this.accumulationtotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }
  //担保信息
  guarantee() {
    let param2 = {
      clientNo: this.inValue.clientNo,
      pageSize: this.pageSize,
      pageNum: this.pageNum,
      bussType: '01',
      contractNo: this.inValue.contractNum
      // contractNo:'0117005770'
    }
    //担保信息
    this.httpService.BZ_0200300000403_LOAN(param2).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.loanGuarInfoItems = data.queryResultArray;
        this.loanGuarInfopageNum = data.pageNum;
        this.loanGuarInfopageSize = data.pageSize;
        this.loanGuarInfototalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }

  //共同借款人
  borrow() {
    let param3 = {
      // clientNo: this.inValue.clientNo,
      // clientFlag: '1',
      // tranType: '0',
      // pageNum: this.pageNum,
      // pageSize: this.pageSize,
      // contractNo:this.inValue.contractNum
      appUserId: this.userId,
      consumerSeqNo: this.inValue.seqNo
    }
    //共同借款人
    this.httpService.BZ_0200300002003_LOAN(param3).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.borrowerItems = data.infoList;
        this.borrowerpageNum = data.pageNum;
        this.borrowerpageSize = data.pageSize;
        this.borrowertotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }

  showDetail(data) {
    this.activeShow = data;
    this.myVar = data;
    if (data == 1 && this.inValue) {
      this.refund()
    }
    if (data == 2 && this.inValue) {
      this.guarantee()
    }
    if (data == 3 && this.inValue) {
      this.borrow()
    }
    if (data == 4 && this.inValue) {
      this.account()
    }
  }

  paginaterepaymentsitems(event) {
    this.repaymentsitemspageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.repaymentsitemspageNum = currentPage;
    this.httpService.BZ_0200300000104_LOAN({ duebillNo: this.inValue.duebillNo, clientNo: this.inValue.custNo, bussType: '0', pageSize: this.repaymentsitemspageSize, pageNum: this.repaymentsitemspageNum }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.repaymentsitems = data.infoList;
        this.repaymentsitemspageNum = data.pageNum;
        this.repaymentsitemspageSize = data.pageSize;
        this.repaymentsitemstotalNum = data.totalNum;
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }
  paginateborrowerItems(event) {
    this.borrowerpageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.borrowerpageNum = currentPage;
    let param3 = {
      clientNo: this.inValue.clientNo,
      clientFlag: '1',
      tranType: '0',
      pageNum: this.borrowerpageNum,
      pageSize: this.borrowerpageSize,
      contractNo: this.inValue.contractNum
    }
    //共同借款人
    this.httpService.BZ_0200300002003_LOAN(param3).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.borrowerItems = data.infoList;
        this.borrowerpageNum = data.pageNum;
        this.borrowerpageSize = data.pageSize;
        this.borrowertotalNum = data.totalNum;
      }
    });
  }
  //转码
  codeValue(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['label'] == val) {
          temp = item['value']
        }
      });
    }
    return temp
  }

  toCustView(data) {
    if (data.clientNo) {
      this.commfunc.setSessionData('custNo', data.clientNo);
    }
    if (data.relaCustNo) {
      this.commfunc.setSessionData('custNo', data.relaCustNo);
    }
    this.router.navigate(['pages/custom-view']).then(data => {
      window.location.reload();
    });
  }
  //贷前
  beforeLoan() {
    this.activeShow = 5;
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    console.log(this.tellSeqNo + "    " + this.clientNo + "    " + this.newdate + "   " + this.biaozhi)
    if (this.clientNo && this.tellSeqNo && this.newdate && this.biaozhi) {
      let url = "http://10.1.97.166:9081/SunIAS_V7/CreditImgRequestServlet.do?UID=MOBILE&PWD=cyg&AppID=APP1009&UserID=" + this.userId + "&UserName=" + this.userName + "&OrgID=" + this.orgId + "&OrgName=" + this.orgName + "&info1=BUSI_SERIAL_NO:" + this.tellSeqNo + ";CUSTOMER_NO:" + this.clientNo + ";OBJECT_NAME:APP1009;SERIAL_TYPE:DQ;RIGHT:0100000;FILELEVEL:18;QUERY_TIME:" + this.newdate + ";BANK_FLAG:" + this.biaozhi + ";";
      let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
      let closeOnLoseFocus = 'closeOnLoseFocus';
      this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
    } else {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: "影像链接参数不全!" }];
    }
  }
  //贷后
  afterLoan() {
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    if (this.clientNo && this.tellSeqNo && this.newdate && this.biaozhi) {
      let url = "http://10.1.97.166:9081/SunIAS_V7/CreditImgRequestServlet.do?UID=MOBILE&PWD=cyg&AppID=APP1009&UserID=" + this.userId + "&UserName=" + this.userName + "&OrgID=" + this.orgId + "&OrgName=" + this.orgName + "&info1=BUSI_SERIAL_NO:dh_" + this.tellSeqNo + ";CUSTOMER_NO:" + this.clientNo + ";OBJECT_NAME:APP1009;SERIAL_TYPE:DQ;RIGHT:0100000;FILELEVEL:18;QUERY_TIME:" + this.newdate + ";BANK_FLAG:" + this.biaozhi + ";";
      let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
      let closeOnLoseFocus = 'closeOnLoseFocus';
      this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
    } else {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: "影像链接参数不全!" }];
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  custNumClick() {
    if (this.commfunc.getSessionDataCH('custNoNum') && this.commfunc.getSessionDataCH('custNoNum') == '1') {
      this.commfunc.setSessionDataCH('custNoNum', '2');
    }
  }

  compileStr(cust) {
    return this.commfunc.compileStr(cust)
  }
}
